import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import cookie from "js-cookie";
import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = [];

/*
    Example: <srs-sessionvalidator cookie_name="mysessionid" alert_when=60000/>
*/
const SrsSessionValidator = function (props, ref) {
    const [expiryTime, setExpiryTime] = useState("");

    useEffect(() => {
        const interval = setInterval(() => {
            const sessionLastTouched = parseInt(cookie.get(props.cookie_name));
            const sessionTimeoutInterval = parseInt(cookie.get("SESSION_TIMEOUT")) || 3600000;
            const now = Date.now();

            if (sessionLastTouched && now > sessionLastTouched + sessionTimeoutInterval) {
                alert("Session has expired. Please login again.");
                clearInterval(interval);
                props.dispatchEvent(EventAPI.SESSION_EXPIRED, {});
            } else {
                const timeLeft = Math.ceil((sessionLastTouched + sessionTimeoutInterval - now) / (60*1000));
                setExpiryTime(`${timeLeft} minutes`);
            }
        }, 10000);
        return () => clearInterval(interval);
    }, []);

    if (expiryTime) {
        return <div>Your session will expire in {expiryTime} minutes.</div>;
    } else {
        return null;
    }
}

const styles = `
    
`
const props = [{
    name: "cookie_name",
    defaultValue: "",
    type: "string"
}, {
    name: "alert_when",
    defaultValue: "",
    type: "number"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsSessionValidator), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };